$(document).ready(function(){
    $(".owl-carousel-slider").owlCarousel({
        loop:true,
        margin:10,
        responsiveClass:true,
        responsive:{
            0:{
                items:1,
            },
            600:{
                items:3,
            },
            1000:{
                items:5,
                loop:false,
            }
        }
    });
    $(".test").owlCarousel({
        loop:true,
        margin:10,
        responsiveClass:true,
        responsive:{
            0:{
                items:1,
            },
            600:{
                items:3,
            },
            1000:{
                items:5,
                loop:false,
            }
        }
    });

    $('.bieanale-open__slider').owlCarousel({
        center: true,
        items: 1,
        loop:true,
        margin:10,
    });

    $('#nav-icon1').click(function(){
        $(this).toggleClass('open');
        $(".dropdown-mobile-menu").toggleClass('active-menu');
        $("body").toggleClass("no-scroll");
        $(".header-mobile-menu_left").toggleClass("no-visible");
    });

    $(window).on('resize, load', function() {
        if ($(window).width() < 768) {
           $('.footer-logo').insertBefore($('.footer_menu>ul'));
           $('.footer_bottom__links').insertAfter($('.footer_menu>ul'));
        }
        if ($(window).width() < 576) {
            $('.footer_bottom__contacts').insertAfter($('.footer_menu>ul'));
            $('.ffooter_bottom__links').insertBefore($('.footer_bottom__contacts'));
         }
      })
});